import React, { useState } from 'react';
import InputGroup from 'react-bootstrap/InputGroup';
import FormControl from 'react-bootstrap/FormControl';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Alert from 'react-bootstrap/Alert';
import Spinner from 'react-bootstrap/Spinner';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Star } from 'react-bootstrap-icons';

import { Message } from '../stats/Stats.Component';
import CalificadorMetro from '../calificadormetro/CalificadorMetro.Component';

const NewEvaluacionForm = ({ submit, redirect, clean, account, disabled }) => {

    const [email, setEmail] = useState(account !== null ? account.email : "");
    const [nombre, setNombre] = useState(account !== null ? account.nombre : "");
    const [calificacion, setCalificacion] = useState(5);

    const changeEmail = (e) => setEmail(e.target.value);
    const changeNombre = (e) => setNombre(e.target.value);
    const onSubmit = (e) => submit(email, nombre, calificacion);

    const onClean = (e) => {
        setEmail(""); setNombre("");
        clean();
    }

    const onRedirect = (e) => {
        setEmail(""); setNombre("");
        redirect();
    }

    return (
        <Card>
            <Card.Img variant="top" src="header.png" style={{ maxHeight: 160, borderBottom: 'solid gray', borderBottomWidth: 1 }}/>
            <Card.Body>
                <Alert variant="success">
                    <h5>Hola!</h5>
                    <h5 style={{ fontSize: 15 }}>
                        Selecciona las estrellas en base a tu evaluación de nuestro servicio.
                    </h5>
                </Alert>
                <InputGroup className="mb-3">
                    <InputGroup.Prepend>
                        <InputGroup.Text id="basic-addon1">Email</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        placeholder="test@gmail.com"
                        aria-label="email"
                        aria-describedby="basic-addon1"
                        name="email"
                        onChange={changeEmail}
                        value={email}
                        disabled={disabled || account !== null}
                    />
                </InputGroup>
                <InputGroup className="mb-3">
                    <InputGroup.Prepend>
                        <InputGroup.Text id="basic-addon1">Nombre</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        placeholder="Rodrigo López"
                        aria-label="name"
                        aria-describedby="basic-addon1"
                        name="name"
                        onChange={changeNombre}
                        value={nombre}
                        disabled={disabled || account !== null}
                    />
                </InputGroup>
                <CalificadorMetro onChange={setCalificacion} value={calificacion}/>
                <div style={{ textAlign: 'center', color: 'black', paddingBottom: '10px'}}>{Message(calificacion)}</div>
                <InputGroup className="mb-1">
                    <Button variant="success" block onClick={onSubmit} disabled={disabled}>
                        {disabled ? (
                            <Spinner animation="border" variant="primary" />
                        ): (
                            <Star /> 
                        )} Calificar
                    </Button>
                </InputGroup>
                {account !== null ? (
                    <Row>
                        <Col lg={6}>
                            <InputGroup className="mb-1">
                                <Button variant="outline-dark" block  onClick={onRedirect} disabled={disabled}>
                                    Ver evaluaciones
                                </Button>
                            </InputGroup>
                        </Col>
                        <Col lg={6}>
                        <InputGroup className="mb-1">
                            <Button variant="outline-dark" block  onClick={onClean} disabled={disabled}>
                                Cerrar sesión
                            </Button>
                        </InputGroup>
                        </Col>
                    </Row>
                ): null}
            </Card.Body>
        </Card>
    );
};

export default NewEvaluacionForm;